.nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1300px;
  height: 100px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
    background-color: red;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    background-color: gold;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
    background-color: green;
  }
}
.header-arrow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80px;
  height: 28px;
  position: absolute;
  right: -104px;
  bottom: -395px;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  transform: rotate(90deg);

  @media (max-width: @desktop-width) {
    width: 70px;
    font-size: 16px;
    line-height: 140%;
    position: absolute;
    right: -75px;
    bottom: -320px;
  } 

  @media (max-width: @tablet-width) {
    display: none;
  }
}
.header-arrow-svg {
  display: block;
  transform: rotate(90deg);
}
.nav-list {
  position: absolute;
  right: 70px;
  top: 39px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 665px;

  @media (max-width: @desktop-width) {
    width: 550px;
    right: 65px;
    top: 40px;
  }  

  @media (max-width: @tablet-width) {
    display: none;
  }

  // @media (max-width: @mobile-width) {
  //   width: 328px;
  //   height: auto;
  //   background-color: green;
  // }
}
.nav-item a {
  display: block;
  font-family: "Satoshi-Medium", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  
  &:hover {
    color: @basic-blue;
    border-bottom: 1px solid @basic-blue;
  }

  &:active {
    border: none;
  }
  
  @media (max-width: @desktop-width) {
    font-size: 14px;
    line-height: 19px;
  }

  @media (max-width: @tablet-width) {
    width: 149px;
    margin-top: 40px;
    margin-left: 335px;
    font-family: "Satoshi-Medium", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    color: @background_page;
    text-align: left;

  }

  @media (max-width: @mobile-width) {
    margin-top: 20px;
    margin-left: 250px;
    margin-bottom: 25px;
    // width: 57px;
    height: auto;
    font-size: 16px;
    line-height: 140%;
  }
}
.nav-active a {
  color: @basic-blue;
}
.nav-active {
  color: @basic-blue;
}
.nav-button-contact {
  display: block;
  width: 50px;
  height: 50px;
  margin-right: 8px;


  @media (max-width: @desktop-width) {
    margin: 0;
  }

  @media (max-width: @tablet-width) {
    margin-right: -480px;
  }

  @media (max-width: @mobile-width) {
    margin-right: -142px;
    width: 36px;
    height: 36px;
  }
}
