.burger__line {
  display: block;

  z-index: 100;
  background-color: #000;
  position: absolute;
  right: 0;
  transition: .5s;
}
.burger__line_first {
  top: 0;

}
.burger__line_second {
  top: 9px;
  width: 30px;
  height: 2px;
  // transform: translateY(-50%);
}
.burger__line_third {
  top: 20px;
  width: 20px;
  height: 2px;
  // transform: translateY(-50%);
}


.burger_active .burger__line_second {
  top: 15px;
  transform: translateY(-50%) rotate(45deg);
  background-color: #FDFDFD;
}

.burger_active .burger__line_third {
  top: 15px;
  width: 30px;
  transform: translateY(-50%) rotate(-45deg);
  background-color: #FDFDFD;
}



