.nav-opened {
  position: absolute;
  z-index: 1;
  top: 0;
  right: -332px;
  display: block;
  width: 1364px;
  height: 100vh;
  background-color: @basic_dark;
  overflow: hidden;

  animation-name: menu;
  animation-duration: .5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;


  @keyframes menu{
    0%{
      right: -1080px;
    }
  
    100%{
      right: -333px;
    }
  }

  @media (max-width: @mobile-width) {
    position: absolute;
    z-index: 1;
    top: 0;
    right: -220px;
    display: block;
    width: 800px;
    height: 100vh;
    background-color: @basic_dark;

    animation-name: menu;
    animation-duration: .5s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
  
    @keyframes menu{
      0%{
        right: -1080px;
      }
    
      100%{
        right: -233px;
      }
    }
  }
}

.nav-item-head {
  display: none;

  @media (max-width: @tablet-width) {
    display: flex;  
    justify-content:space-between;
    align-items: center;
    margin: 0 auto;
    width: 700px;
    height: 100px;
    margin-bottom: 72px;
  }

  @media (max-width: @mobile-width) {
    position: relative;
    margin-bottom: 0;
    width: 328px;
    height: 100px;
  }
}

.nav-img-head {


  @media (max-width: @tablet-width) {
    display: block;
    width: 70px;
    height: 70px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    width: 50px;
    height: 50px;
    margin-left: 18px;
    // margin-right: 155px;
    // margin-top: 19px;
  }
}
.nav-img-head-logo {
  display: none;

  @media (max-width: @mobile-width) {
    display: block;
    width: 50px;
    height: 50px;
  }
}
.nav-button-contact-head {

  @media (max-width: @tablet-width) {
    display: block;
    margin-right: 65px;
    // margin-top: 10px;
  }

  @media (max-width: @mobile-width) {
    margin-right: 50px;
    margin-top: 0;
    width: 36px;
    height: 36px;
  }
}
.nav__toggle-head {
  display: none;


  @media (max-width: @tablet-width) {
    display: block;
    content: "";
    position: absolute;
    z-index: 5;
    top: 48px;
    right: 37px;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    transform: rotate(45deg);

    &::before {
      content: "";
      position: absolute;
      z-index: 4;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
      transform: rotate(-90deg);
    }
  }

  @media (max-width: @mobile-width) {
    display: block;
    position: absolute;
    z-index: 5;
    top: 45px;
    right: -2px;
    background-color: #303030;
    width: 30px;
    height: 30px;

    &::after {
    content: "";
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
    }
  }
}
.nav-item-social {
  display: none;

  @media (max-width: @tablet-width) {
    position: relative;
    display: block;
    width: 313px;
    height: 70px;
    margin-left: 335px;
    margin-top: 268px;
    color: @background_page;
    font-family: "Satoshi-Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    text-transform: uppercase;
    text-align: left;


    &::after {
      content: "";
      position: absolute;
      top: -50px;
      left: -335px;
      width: 1364px;
      height: 1px;
      background-color: #A0A0A0;
    }
  }

  @media (max-width: @mobile-width) {
    display: none;
  }
}
